<template>
    <div>
        <h3 class="title">{{ title }}</h3>
        <div class="player">
            <iframe ref="video" :src="url" allow='autoplay' allowfullscreen frameborder="0"></iframe>
        </div>
    </div>
</template>

<script>
    export default {
        name: "VideoPlayer",
        props: {
            id: {
                type: String,
                required: true
            },
            name: {
                type: String,
                required: true
            }
        },
        computed: {
            url() {
                return 'https://drive.google.com/file/d/' + this.id + '/preview';
            },
            title() {
                return 'Détective Conan épisode ' + this.name + ' VOSTFR';
            }
        }
    }
</script>

<style lang="scss" scoped>
    .player {
        position: relative;
        overflow: hidden;
        padding-top: 56.25%;

        iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border: 0;
        }
    }

    .title {
        color: white;
        font-size: 18pt;
        padding-bottom: 0.3em;
    }
</style>
