<template>
    <b-container class="content px-0 pt-4" v-if="episodes">
        <video-player class="py-3" :id="currentEpisode.id" :name="currentEpisode.name"></video-player>

        <span class="float-right pr-3">
            <span class="text-white">Choisir épisode : </span>
            <b-select class="episode-chooser" v-model="$store.state.episode" @input="$store.commit('setEpisode', currentEpisode.name)">
                <option v-for="episode in episodes" :key="episode.name">
                    {{ episode.name }}
                </option>
            </b-select>
        </span>
    </b-container>
</template>

<script>
    export default {
        name: "Episodes",
        computed: {
            currentEpisode() {
                return this.episodes.filter(e => e.name === this.$store.getters.currentEpisode.toString()).pop();
            },
            episodes() {
                return this.$store.getters.episodes;
            }
        }
    }
</script>

<style lang="scss" scoped>
    .content {
        height: 100%;
        background: rgba(255, 255, 255, 0.1);
    }

    .episode-chooser {
        width: 100px;
    }
</style>
